body,
h1 {
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
  
h1 {
  text-align: center;
  padding-top: 20px;
  font-size: 30px;
  text-transform: uppercase;
}

.page__watch {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(- 50%, -50%);
}

@import "./utils/variables";
@import "./utils/mixins";
@import "./utils/extends";
@import "./utils/functions";

@import "./watch.scss";
